<template>
<div class="app-page auto-distribution-detail">
    <page-content class="page-content">
      <scroller style=""
                :on-refresh="refresh"
                ref="myscroller">
        <div class="list">
        <div class="item" v-for="(item, index) in list" :key="index">
          <van-icon class="icon-passed" name="passed" size=".6rem" color="#cccccc"/>
          <img src="" alt="">
          <page-line v-if="index !== list.length-1" direction="V" borderStyle="dashed" :length="3.25" color="#cccccc"></page-line>
          <div class="item-left">
            <div class="left-top">
              橙子橙子天然维C超甜橙 子橙子天然维C超甜
            </div>
            <div class="left-center">
              规格：3kg
            </div>
            <div class="left-bottom">
              本次配送时间：01-06 星期天
            </div>
          </div>
          <div class="item-right">
            <div class="right-1">
              <span>¥36.00</span>
            </div>
            <div class="right-2">
              <span>x1</span>
            </div>
            <div class="right-3 flex-center">
              <van-button>取消订单</van-button>
            </div>
          </div>
        </div>
      </div>
      </scroller>
    </page-content>
</div>

</template>
<script>
import PageContent from '@/components/page/content'
import PageLine from '@/components/page/line'
export default {
  name: 'autoDistributionDetail',
  components: { PageContent, PageLine },
  data () {
    return {
      list: [1, 1, 1, 1, 1, 1, 1, 1]
    }
  },
  methods: {
    /** 下拉刷新相关方法****/
    refresh (done) {
      console.log('下拉刷新')
      setTimeout(() => {
        done()
      }, 500)
    }
  },
  created () {
  }
}

</script>

<style rel='stylesheet/scss' lang='scss' scoped>
  @import '@/styles/variables.scss';
  .auto-distribution-detail{
    font-family: $font-family-default!important;
    .page-content{
      background-color: #f9f9f9;
      padding: 0 0 0 0;
      .list{
        padding-left: 90px;
        .item+.item{
          /*margin-top: 0.5rem;*/
        }
        .item{
          position: relative;
          /*width: 100%;*/
          height: 200px;
          padding: 20px 20px 30px 20px;
          margin: 25px 25px 0 0;
          display: flex;
          color: $black999999;
          background-color: #fff;
          border-radius: 8px;
          .item-left{
            flex: 2;
            height: 100%;
            display: flex;flex-direction: column;justify-content: space-between;
            .left-top{
              font-size: 29px;
              line-height: 1.1;
            }
            .left-center{
              font-size: 24px;
            }
            .left-bottom{
              font-size: 24px;
            }
          }
          .item-right{
            flex: 1;
            height: 100%;
            position: relative;
            .right-1{
              font-size: 29px;
              display: flex;flex-direction: row-reverse;
            }
            .right-2{
              font-size: 24px;
              display: flex;flex-direction: row-reverse;
            }
            .right-3{
              position: absolute;bottom: 0;right: 0;
              width: 150px;height: 54px;
              /deep/ .van-button{
                width: 100%;height: 100%;
                line-height: 0;
                font-size: 20px;
                padding: 0 0;
                border-radius: 16px;
                border: 1.2px solid $black999999;
                color: $black666666;
                span{
                  letter-spacing: 0;
                }
              }
            }
          }
          .icon-passed{
            position: absolute;
            top: calc(50% - .3rem);left: -.8rem;
            background-color: $whiteffffff;
            z-index: 20;
          }
          >.line{
            position: absolute;
            left: -.5rem;top: 50%;
            z-index: 10;
          }
        }
      }
    }
  }
</style>



